var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"graphism"},[_c('h1',[_vm._v("Graphisme")]),_c('div',{staticClass:"wrapper"},[_c('div',{staticClass:"project"},[_c('Project',{attrs:{"title":"Dessins","subtitle":"Loisirs","picture":_vm.dessin,"blabla":"Tout au long de mes études supérieures, j’ai réalisé de nombreux dessins, en essayant plusieurs techniques différentes (aquarelles, posca, encre, etc.). J’ai toujours beaucoup aimé dessiner et tester de nouvelles choses. J’ai d’ailleurs créé un compte Instagram d’abord dédié uniquement à mes dessins, puis je me suis diversifié avec d’autres projets.","img":_vm.dessinpic,"hidimg":true,"lien":"https://www.instagram.com/poulpillow/","hidlink":true,"nom":"Instagram : @poulpillow"}})],1),_c('div',{staticClass:"project"},[_c('Project',{attrs:{"title":"Game Jam Color","subtitle":"Février 2022 - Événement en 48h ","picture":_vm.gamejam,"blabla":"Lors d’un week-end, en seulement 48h, nous avons participé avec des amis à la Game Jam Color de l’association 803Z de l’université Gustave Eiffel de Champs-sur-Marne.\nNous devions réaliser un jeu vidéo avec pour thème “La cour de récréation” et comme contrainte d’utiliser en couleur dominante du jeu l’ocre. J’ai été en charge lors de l’événement de tous les graphismes du jeu.\nLe jeu que nous avons réalisé est un jeu qui ressemble à “Little Alchemy” ou “Doodle God” où il faut assembler des objets ensembles pour se sortir de situations plutôt délicates.\nEn tout, j’ai dû dessiner une soixantaine d’objets, six décors et une jaquette. Tous mes dessins ont été réalisés sur le logiciel Krita.\nCe défi fut très enrichissant, car il m’a aussi fallu créer une charte graphique à respecter, ainsi qu’une palette de couleur avec environ 10 couleurs à utiliser, ce qui m’a permis de tester mes capacités en un temps limité.\nNous avons gagné le prix du “Petit Alchimiste en Herbe”.","img":_vm.gamejampic,"hidimg":true,"lien":"https://just-kiel.itch.io/childchemy","hidlink":true,"nom":"Page du jeu"}})],1),_c('div',{staticClass:"project"},[_c('Project',{attrs:{"title":"Cartographie","subtitle":"Janvier 2021 - Projet IMAC","picture":_vm.carto,"blabla":"Toujours en Art Appliqués, nous devions réaliser un projet en deux temps. Tout d’abord il nous fallait réaliser un Guide des styles avec des règles graphiques à respecter explicitées. Nous avons pour cela utilisé InDesign.\nEnsuite, nous devions faire une cartographie d’un lieu tout en changeant totalement de style et en expliquant notre démarche. Nous avons utilisé Illustrator et Krita pour faire la carte.\nNous avons choisi avec mon binôme de faire une carte des Machines de l’Îles de Nantes dans un style enfantin et très simplifié, à destination des enfants qui souhaitent voir le Caroussel et l'Éléphant Mécanique. Nous avons eu une grande phase de réflexion sur ce que nous voulions faire et nous sommes très fiers du résultat. Je me suis principalement occupé de dessiner toutes les icônes, et de choisir la disposition des objets.","img":_vm.cartopic,"hidimg":true}})],1),_c('div',{staticClass:"project"},[_c('Project',{attrs:{"title":"Glitch","subtitle":"Novembre 2020 - Projet IMAC","picture":_vm.glitch,"blabla":"En première année à l’IMAC, nous avions un projet en binôme à réaliser en Art Appliqués. Nous devions créer des moodboards sur le thème du glitch, puis créer 5 œuvres et expliquer notre travail et nos choix artistiques et esthétiques. Nous avons utilisé InDesign et Illustrator de la suite Adobe. ","img":_vm.glitchpic,"hidimg":true}})],1)])])}
